// External imports, installed libraries, etc
import React from 'react';
import styled from 'styled-components';

// Internal data, functions, custom hooks, etc
import navigationConfig from '../Navigation/config';
import colors from '../../styles/colors';

// Internal components, images, etc
import Logo from '../assets/LogoIcon';
import _NavTelephone from '../NavTelephone';
import NavToggle from '../NavToggle';
import Link from '../elements/Link';

const TopBar = styled.div`
	align-items: center;
	background: ${colors.white};
	display: flex;
	justify-content: space-between;
	height: ${navigationConfig.navTopbarHeight};
	margin-bottom: 1rem;
	padding: 0 ${navigationConfig.navTopbarPadding.unit}${navigationConfig.navTopbarPadding.unitType};
	position: relative;
	z-index: 999;

	&::after {
		bottom: 0;
		border-bottom: solid 2px ${colors.grey[2]};
		content: ' ';
		left: 50%;
		position: absolute;
		transform: translateX(-50%);
		width: calc(100% - ${navigationConfig.navTopbarPadding.unit * 2}${navigationConfig.navTopbarPadding.unitType});
		z-index: 1;
	}
`;

const NavTelephone = styled(_NavTelephone)`
	margin-right: 20px;
`;

const Left = styled.div`

`;

const Right = styled.div`
	
`;



const NavTopbar = ( {isMobile, navIsOpen, toggleNav} ) => {
	return (
		isMobile ? (
			<TopBar>
				<Left>
					<Logo/>
				</Left>
				<Right>
					<NavTelephone isMobile={isMobile} title={'Call us on 01484 715063'} href={'tel:01484715063'}/>
					<NavToggle isMobile={isMobile} navIsOpen={navIsOpen} toggleNav={toggleNav}/>
				</Right>
			</TopBar>
		) : (
			<TopBar>
				<Left>
				<NavTelephone isMobile={isMobile} href={'tel:01484715063'}>Call us: 01484 715063</NavTelephone>
				</Left>
				<Right>
					<Link href='/contact'>12 Bethel Street, Brighouse, HD6 1JN</Link>
				</Right>
			</TopBar>
		)
	);
}

export default NavTopbar;
