// External imports, installed libraries, etc
import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';

// Internal data, functions, custom hooks, etc
import colors from '../../../styles/colors';

// Internal components, images, etc
// NA ATM


const Paragraph = styled.p`
	color: ${props => props.color || colors.font};
	font-family: ${props => props.type === 'serif' ? 
				 ('"Cormorant Garamond", "Times New Roman", Times, serif') :
				 ('"Raleway", "Segoe UI",Tahoma,Geneva,Verdana,sans-serif')};
	font-weight: 400;
	font-size: ${props => props.type === 'sans' && ('.95rem')};
	line-height: 1.9rem;
	margin-bottom: 1.25em;
`;

const Component = ({ className, children, color, type }) => {
	return (
		<Paragraph color={color} className={className} type={type}>
			{children}
		</Paragraph>
	);
}

export default Component;

Component.defaultProps = {
	type: 'sans',
};

Component.propTypes = {
	type: PropTypes.string,
};
