// External imports, installed libraries, etc
import React, { useState, useEffect } from 'react';
import styled from 'styled-components';

// Internal data, functions, custom hooks, etc
import navigationConfig from '../Navigation/config';
import useWindowSize from '../../hooks/useWindowSize';

// Internal components, images, etc
import NavTopBar from '../NavTopbar';
import LogoFullStacked from '../assets/LogoFullStacked';
import _NavBar from '../NavBar';

const Nav = styled.nav`

`;

const LogoBar = styled.div``;

const NavBar = styled(_NavBar)`
	height: ${navigationConfig.navTopbarHeight};
`;

const Navigation = () => {
	const { width: currentWidth } = useWindowSize();

	const [width, setWidth] = useState(currentWidth);
	const [navIsOpen, setNavIsOpen] = useState(false);

	const isMobile = width < navigationConfig.navigationResponsiveBreakPoint;

	const toggleNav = (e) => {
		navIsOpen ? (
			setNavIsOpen(false)
		) : (
			setNavIsOpen(true)
		)
	};

	useEffect(() => {
		if (currentWidth !== width) {
			setWidth(currentWidth);
		}
	}, [currentWidth, width]);
	
	useEffect(() => {
		!isMobile && (
			setNavIsOpen(false)
		)
	}, [isMobile]);

	return (
		<Nav>
			<NavTopBar isMobile={isMobile} navIsOpen={navIsOpen} toggleNav={toggleNav} />
			{!isMobile && (
				<LogoBar>
					<LogoFullStacked/>
				</LogoBar>
			)}
			<NavBar isMobile={isMobile} navIsOpen={navIsOpen} navItems={navigationConfig.pages} />
		</Nav>
	);
}

export default Navigation;
